@import url(https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC);
.App {
  padding: 1em;
}

label {
  display: inline-block;
  width: 100px;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

header, h1 {
  font-family: 'Playfair Display SC';
  font-size: 64px;
  display: flex;
  justify-content: center;
}

/* typography */

html {
  font-family: 'helvetica neue', sans-serif;
}

thead th, tfoot th {
  font-family: 'Rock Salt';
}

th {
  letter-spacing: 2px;
  padding: 10px;
}

input {
  max-width: 100%;
}

td {
  letter-spacing: 1px;
  overflow-x:hidden;
  overflow-y:hidden;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow:hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.content_td p {
  max-width: 100%;
  max-height: 100px;
  overflow-y: auto;
  text-overflow: ellipsis;
}

.scale-down {
  object-fit: scale-down;
  width: 100%;
  height:100%;
}

/* Column Size for thead */
th:nth-child(4), th:nth-child(5) {
  width: 30%;
}

/* Column size for tbody */
td:nth-child(5), td:nth-child(4) {
  width: 30%;
}
.recipe-list {
  margin-top: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead th {
  background-color: #f8f8f8;
  color: #333;
  font-weight: bold;
  padding: 10px;
  text-align: center;
}

tbody tr:nth-child(odd) {
  background-color: #fff0c7;
}

tbody td {
  padding: 10px;
}

.content-td {
  width: 30%;
  max-width: 300px;
  overflow: auto;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

button[name="delete"] {
  background-color: #e74c3c;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button[name="delete"]:hover {
  background-color: #c0392b;
}

